<template>
  <div>
    <v-card
      class="accent mb-5 pa-0 mx-5 rounded-lg"
      elevation="0"
      v-for="(textfield, index) in form"
      :key="index"
    >
      <v-card-text
        ><v-layout row wrap class="mb-n5">
          <v-flex xs8 sm8 md8
            ><v-text-field
              rounded
              v-model="textfield.title"
              label="Label"
              solo
              class="ma-2"
              flat
            ></v-text-field></v-flex
          ><v-flex xs4 sm4 md4
            ><v-select
              rounded
              v-model="textfield.type"
              :items="items"
              solo
              item-text="title"
              class="ma-2"
              flat
            ></v-select></v-flex
        ></v-layout>

        <v-layout row class="mt-n8">
          <v-slider
            :disabled="
              textfield.type == 'E-Mail' ||
              textfield.type == 'Date' ||
              textfield.type == 'Number' ||
              textfield.type == 'Password'
            "
            thumb-label
            v-model="textfield.max"
            class="mt-4 mx-4"
            color="accent darken-2"
            label="Max characters"
          >
          </v-slider>

          <v-switch
            v-model="textfield.required"
            class="font-weight-medium mr-5"
            inset
            :class="!$vuetify.theme.dark ? 'white--text' : 'black--text'"
            :color="$vuetify.theme.dark ? 'black' : 'white'"
            :label="'Required'"
          ></v-switch></v-layout
      ></v-card-text>
      <v-divider></v-divider>
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-tooltip bottom v-if="textfield.type === 'Date'">
          <template v-slot:activator="{ on, attrs }">
            <v-btn
              icon
              class="mr-2"
              @click="textfield.minCurrentDay = !textfield.minCurrentDay"
            >
              <v-icon
                :class="textfield.minCurrentDay ? '' : 'white--text'"
                v-bind="attrs"
                v-on="on"
              >
                {{
                  textfield.minCurrentDay
                    ? " mdi-calendar-lock"
                    : "mdi-calendar-lock-open"
                }}
              </v-icon></v-btn
            >
          </template>
          <span>Date input cannot be before current day</span>
        </v-tooltip>
        <v-tooltip bottom>
          <template v-slot:activator="{ on, attrs }">
            <v-btn
              icon
              class="mr-2"
              @click="textfield.dense = !textfield.dense"
            >
              <v-icon
                :class="textfield.dense ? '' : 'white--text'"
                v-bind="attrs"
                v-on="on"
              >
                mdi-image-size-select-small
              </v-icon></v-btn
            >
          </template>
          <span>Dense</span>
        </v-tooltip>
        <v-tooltip bottom>
          <template v-slot:activator="{ on, attrs }">
            <v-btn
              icon
              class="mr-2"
              @click="textfield.outlined = !textfield.outlined"
            >
              <v-icon
                :class="textfield.outlined ? '' : 'white--text'"
                v-bind="attrs"
                v-on="on"
              >
                mdi-card-outline
              </v-icon></v-btn
            >
          </template>
          <span>Outlined</span>
        </v-tooltip>
        <v-tooltip bottom>
          <template v-slot:activator="{ on, attrs }">
            <v-btn
              icon
              class="mr-2"
              @click="textfield.rounded = !textfield.rounded"
            >
              <v-icon
                :class="textfield.rounded ? '' : 'white--text'"
                v-bind="attrs"
                v-on="on"
              >
                mdi-rounded-corner
              </v-icon></v-btn
            >
          </template>
          <span>Rounded Corners</span>
        </v-tooltip>
        <v-divider vertical></v-divider>
        <v-btn
          icon
          class="red--text ml-2 mr-2"
          @click.stop="$emit('removeField', index)"
          :disabled="index === 0"
        >
          <v-icon>mdi-delete</v-icon>
        </v-btn>
      </v-card-actions></v-card
    >
    <v-layout justify-end class="mr-5">
      <v-btn
        elevation="0"
        rounded
        color="accent"
        @click.stop="$emit('addField')"
        :class="$vuetify.theme.dark ? 'white--text' : 'black--text'"
      >
        <v-icon size="25px" class="mr-2">mdi-plus-circle</v-icon>Add text field
      </v-btn></v-layout
    >
  </div>
</template>

<script>
export default {
  components: {},
  props: { form: Array },
  data: () => ({
    items: [
      { title: "Number" },
      { title: "Text" },
      { title: "Password" },
      { title: "E-Mail" },
      { title: "Date" },
    ],
  }),
  methods: {},
};
</script>
<style></style>
